<template>
  <div class="login-wrap">
    <div class="ms-login">
      <div class="ms-title">仓单业务风险管理系统</div>
      <el-form
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
        label-width="0px"
        class="ms-content"
      >
        <el-form-item prop="username">
          <el-input
            v-model="ruleForm.username"
            placeholder="用户名"
            @change="function(modelVal){ruleForm.username=modelVal}"
          >
            <el-button
              slot="prepend"
              icon="el-icon-all-user"
            ></el-button>
          </el-input>
        </el-form-item>
        <el-form-item prop="password">
          <el-input
            type="password"
            placeholder="密码"
            v-model="ruleForm.password"
            @keyup.enter.native="submitForm('ruleForm')"
            @change="function(modelVal){ruleForm.password=modelVal}"
          >
            <el-button
              slot="prepend"
              icon="el-icon-all-password"
            ></el-button>
          </el-input>
        </el-form-item>
        <div class="login-select">
          <el-checkbox
            v-model="checked"
            style="color:#a0a0a0;"
          >记住帐户</el-checkbox>
          <span @click="findPwd" class="forgetPwd">忘记密码</span>
        </div>
        <div class="login-btn">
          <el-button
            type="primary"
            @click="submitForm('ruleForm')"
          >登录</el-button>
        </div>
        <p class="login-tips">Tips : 请输入用户名和密码。</p>
      </el-form>
    </div>
  </div>
</template>

<script>
import * as ajaxUrl from "../ajax.config.js"; // 引入接口地址

export default {
  data: function() {
    return {
      checked: false,
      ruleForm: {
        username: "",
        password: ""
      },
      rules: {
        username: [
          { required: true, message: "请输入用户名", trigger: "blur" }
        ],
        password: [{ required: true, message: "请输入密码", trigger: "blur" }]
      }
    };
  },
  mounted() {
    this.getUsername();
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
            let postData = {
                username: this.ruleForm.username,
                password: this.$md5(this.ruleForm.password)
            }
            console.log("1")
            localStorage.setItem("aide_username", "system");
            console.log("2")
            this.$router.push("/");
            console.log("3")
            // this.axios.post(ajaxUrl.LOGIN, postData)
            // .then((res) => {
            //     localStorage.setItem("aide_username", res.data.resData.userName);
            //     this.$router.push("/");
            // }).catch((err) => {
            //     console.log(err)
            // })
          
        //   this.$router.push("/");
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },

    //忘记密码
    findPwd() {

    },

    //取得localStorrage记住的用户名
    getUsername() {
      this.ruleForm.username = localStorage.getItem("aide_username");
    }
  }
};
</script>

<style scoped>
.login-wrap {
  position: relative;
  width: 100%;
  height: 100%;
  background-image: url(../assets/images/login-bg.jpg);
  background-size: 100%;
}
.ms-title {
  width: 100%;
  line-height: 50px;
  text-align: center;
  font-size: 20px;
  color: #fff;
  border-bottom: 1px solid #ddd;
}
.ms-login {
  position: absolute;
  left: 50%;
  top: 50%;
  width: 350px;
  margin: -190px 0 0 -175px;
  border-radius: 5px;
  background: rgba(255, 255, 255, 0.3);
  overflow: hidden;
}
.ms-content {
  padding: 30px 30px;
}
.login-btn {
  text-align: center;
  margin-top: 10px;
}
.login-btn button {
  width: 100%;
  height: 36px;
  margin-bottom: 10px;
}
.login-tips {
  font-size: 12px;
  line-height: 30px;
  color: #fff;
}
.forgetPwd {
  cursor: pointer;
  color: #f19149;
  font-size: 0.9rem;
  margin-left: 145px;
}
</style>


